html {
  background-color: fff;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.people {
  justify-content: center;
  align-items: center;
  display: flex;
}

.person {
  width: 200px;
  transition: all 1s;
}

.person__photo {
  position: relative;
}

.person__photo-image {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: all 1s;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.person__details {
  color: #fff;
  text-align: center;
  font-size: 21.6px;
  font-weight: 400;
}

.person__name {
  color: #2f4f4f;
  font-size: 20px;
  font-weight: 700;
}

.person__extra {
  transition: all 1s;
}

.container {
  box-sizing: border-box;
  grid-template-columns: repeat(2, 200px);
  place-content: center;
  gap: 30px 40px;
  height: 100%;
  display: grid;
}

@media (width >= 1000px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.person:hover {
  background-color: #eee;
  border-radius: 12px;
  transform: scale(1.2);
}

.person:hover .person__photo-image {
  border-radius: 60px;
}

.person:hover .person__extra {
  color: #000;
}
